@import "../../Styles/main.scss";

.chip-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: start;
  align-items: center;
  align-content: stretch;

  &__item {
    @include unselectable;

    color: $color-text-light;
    font-size: $default-4;
    font-weight: 500;
    background-color: $color-green;
    border-radius: $round-edges;
    padding: 0.5rem 1rem;
    margin: 0.5rem;
    text-align: center;
    cursor: pointer;

    &--clicked {
      background-color: $color-dark-blue;
    }
  }
}
