@import "../../../../Styles/main.scss";

.utilities {
  &__container {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    align-content: stretch;
    margin-bottom: $spacer-med;
    font-size: 0.75rem;
  }

  &__button {
    position: relative;
    text-transform: uppercase;
    background-color: $color-light-blue;
    color: $color-green;
    border-radius: $round-edges;
    padding: $padding-button;
    font-weight: bolder;
    margin: 0.5rem 0;
    text-align: center;
    cursor: pointer;

    &__clipboard {
      font-weight: 400;

      &--active {
        animation: fade-out-in 1.5s ease-out;
      }
    }

    &__copied {
      @include center-div-absolute;
      opacity: 0;

      &--active {
        animation: fade-in-out 1.5s ease-out;
      }
    }
  }
}
